// lambda/closet/bg-worker.ts
import { S3Event, S3Handler } from "aws-lambda";
import {
  DynamoDBClient,
  QueryCommand,
  UpdateItemCommand,
} from "@aws-sdk/client-dynamodb";
import {
  S3Client,
  GetObjectCommand,
  PutObjectCommand,
} from "@aws-sdk/client-s3";
import fetch from "node-fetch";
import FormData from "form-data";

const ddb = new DynamoDBClient({});
const s3 = new S3Client({});

const REMOVE_BG_URL = "https://api.remove.bg/v1.0/removebg";

const TABLE_NAME = process.env.TABLE_NAME;
const RAW_MEDIA_GSI_NAME = process.env.RAW_MEDIA_GSI_NAME || "rawMediaKeyIndex";
const REMOVE_BG_API_KEY = process.env.REMOVE_BG_API_KEY;
const OUTPUT_BUCKET = process.env.OUTPUT_BUCKET || "";

if (!TABLE_NAME) {
  throw new Error("Missing env: TABLE_NAME");
}
if (!REMOVE_BG_API_KEY) {
  throw new Error("Missing env: REMOVE_BG_API_KEY");
}

const S = (v: string) => ({ S: v });

const nowIso = () => new Date().toISOString();

// --- helpers -----------------------------------------------------

async function streamToBuffer(stream: any): Promise<Buffer> {
  if (!stream) return Buffer.alloc(0);
  if (Buffer.isBuffer(stream)) return stream;

  return new Promise<Buffer>((resolve, reject) => {
    const chunks: Buffer[] = [];
    stream.on("data", (chunk: Buffer) => chunks.push(chunk));
    stream.on("error", reject);
    stream.on("end", () => resolve(Buffer.concat(chunks)));
  });
}

function basename(key: string): string {
  const parts = key.split("/");
  return parts[parts.length - 1] || key;
}

/**
 * Download original from S3, send to remove.bg, upload cutout,
 * return final S3 key.
 */
async function removeBackgroundForS3Object(params: {
  bucket: string;
  key: string;
  destKeyPrefix: string;
}): Promise<string> {
  const { bucket, key, destKeyPrefix } = params;

  console.log("[bg-worker] Downloading original image", { bucket, key });

  const orig = await s3.send(
    new GetObjectCommand({
      Bucket: bucket,
      Key: key,
    })
  );

  const bodyBuf = await streamToBuffer(orig.Body as any);

  console.log("[bg-worker] Calling remove.bg", {
    sizeKb: Math.round(bodyBuf.length / 1024),
  });

  const form = new FormData();
  form.append("size", "auto");
  form.append("format", "png");
  form.append("image_file", bodyBuf, {
    filename: "original.png",
    contentType: orig.ContentType ?? "image/jpeg",
  });

  const resp = await fetch(REMOVE_BG_URL, {
    method: "POST",
    headers: {
      "X-Api-Key": REMOVE_BG_API_KEY!,
    },
    body: form as any,
  });

  if (!resp.ok) {
    const text = await resp.text().catch(() => "");
    console.error("remove.bg failed:", resp.status, text);
    throw new Error(`remove.bg failed (${resp.status})`);
  }

  const cutoutArrayBuf = await resp.arrayBuffer();
  const cutout = Buffer.from(cutoutArrayBuf);

  const destBucket = OUTPUT_BUCKET || bucket;
  const safePrefix = destKeyPrefix.replace(/\/+$/, "");
  const destKey = `${safePrefix}/removed-bg.png`;

  console.log("[bg-worker] Uploading cutout", {
    destBucket,
    destKey,
  });

  await s3.send(
    new PutObjectCommand({
      Bucket: destBucket,
      Key: destKey,
      Body: cutout,
      ContentType: "image/png",
      ACL: "private",
    })
  );

  return destKey;
}

/**
 * Find closet item by rawMediaKey, first using the base filename,
 * then falling back to the full S3 key if needed.
 */
async function findItemByRawMediaKey(
  rawMediaKeyValue: string
): Promise<Record<string, any> | undefined> {
  const q = await ddb.send(
    new QueryCommand({
      TableName: TABLE_NAME,
      IndexName: RAW_MEDIA_GSI_NAME,
      KeyConditionExpression: "rawMediaKey = :rk",
      ExpressionAttributeValues: {
        ":rk": S(rawMediaKeyValue),
      },
      Limit: 1,
    })
  );
  return q.Items?.[0];
}

// --- main record processing --------------------------------------

async function processRecord(bucket: string, key: string): Promise<void> {
  console.log("[bg-worker] Processing S3 object", { bucket, key });

  const base = basename(key);

  // 1) Try to find the closet item by base filename (matches your rawMediaKey)
  let item = await findItemByRawMediaKey(base);

  // 2) Fallback: try full key in case some older items stored that
  if (!item) {
    console.log(
      "[bg-worker] No item for base filename, trying full key",
      base,
      key
    );
    item = await findItemByRawMediaKey(key);
  }

  if (!item) {
    console.warn(
      "[bg-worker] No closet item found for rawMediaKey",
      base,
      "(or full key)",
      key
    );
    return;
  }

  const pk = item.pk?.S || "";
  const sk = item.sk?.S || "META";
  const id = item.id?.S || pk.replace(/^ITEM#/, "");

  if (!pk) {
    console.warn("[bg-worker] item missing pk; skipping", JSON.stringify(item));
    return;
  }

  console.log("[bg-worker] Found closet item", {
    id,
    pk,
    sk,
    rawMediaKey: base,
  });

  // 3) Remove background + upload cutout
  const destKeyPrefix = `uploads/closet/${id}`;
  const mediaKey = await removeBackgroundForS3Object({
    bucket,
    key,
    destKeyPrefix,
  });

  // 4) Update item with mediaKey
  await ddb.send(
    new UpdateItemCommand({
      TableName: TABLE_NAME,
      Key: { pk: S(pk), sk: S(sk) },
      UpdateExpression: "SET mediaKey = :mk, updatedAt = :u",
      ExpressionAttributeValues: {
        ":mk": S(mediaKey),
        ":u": S(nowIso()),
      },
    })
  );

  console.log("[bg-worker] Updated item with mediaKey", { id, mediaKey });
}

// --- Lambda handler ----------------------------------------------

export const handler: S3Handler = async (event: S3Event) => {
  console.log(
    "[bg-worker] Received event",
    JSON.stringify(
      event.Records.map((r) => ({
        bucket: r.s3.bucket.name,
        key: decodeURIComponent(r.s3.object.key.replace(/\+/g, " ")),
      })),
      null,
      2
    )
  );

  for (const record of event.Records ?? []) {
    const bucket = record.s3.bucket.name;
    const key = decodeURIComponent(record.s3.object.key.replace(/\+/g, " "));

    // Your S3 notifications already filter Prefix=users/ & suffixes;
    // no additional guard needed here.

    try {
      await processRecord(bucket, key);
    } catch (err: any) {
      console.error("[bg-worker] Error processing record", {
        bucket,
        key,
        err: err?.message || String(err),
      });
    }
  }
};
